import * as echarts from 'echarts';
export default function (id) {
    var myChart = echarts.init(document.getElementById(id));

    var img = 'data:image/png;base64,';
    var trafficWay = [{
        name: '海淀',
        value: 30
    }, {
        name: '东城',
        value: 25
    }, {
        name: '西城',
        value: 30
    }, {
        name: '朝阳',
        value: 40
    }, {
        name: '丰台',
        value: 30
    }];

    var data = [];
    var color = ['#00ff99', '#23b974', '#006ced', '#ffe004', '#fc4212']
    for (var i = 0; i < trafficWay.length; i++) {
        data.push({
            value: trafficWay[i].value,
            name: trafficWay[i].name,
            itemStyle: {
                normal: {
                    borderWidth: 5,
                    shadowBlur: 20,
                    borderColor: color[i],
                    shadowColor: color[i]
                }
            }
        }, {
            value: 2,
            name: '',
            itemStyle: {
                normal: {
                    label: {
                        show: false
                    },
                    labelLine: {
                        show: false
                    },
                    color: 'rgba(0, 0, 0, 0)',
                    borderColor: 'rgba(0, 0, 0, 0)',
                    borderWidth: 0
                }
            }
        });
    }
    var seriesOption = [{
        name: '',
        type: 'pie',
        clockWise: false,
        radius: [90, 93], 
        hoverAnimation: false,
        itemStyle: {
            normal: {
                label: {
                    show: true,
                    position: 'outside',
                    color: '#ddd',
                    formatter: function (params) {
                        var percent = 0;
                        var total = 0;
                        for (var i = 0; i < trafficWay.length; i++) {
                            total += trafficWay[i].value;
                        }
                        percent = ((params.value / total) * 100).toFixed(0);
                        if (params.name !== '') {
                            return params.name + '\n' + '\n' + percent;
                        } else {
                            return '';
                        }
                    },
                },
                labelLine: {
                    length: 40,
                    length2: 0, 
                    show: false,
                    fontSize: 16,
                }
            }
        },
        data: data
    }];
    var option = {
        backgroundColor: 'rgba(255,255,255,0.0)',
        color: color,
        title: {
            text: '各区交通事故',
            subtext: '366',
            x: 'center',
            y: 'center',
            textStyle: {
                fontWeight: 'normal',
                fontSize: 18,
                color: '#00ff66'
            },
            subtextStyle: {
                fontWeight: 'normal',
                fontSize: 17,
                color: '#00ff66'
            }, 
        },
        graphic: {
            elements: [{
                type: "image",
                z: 3,
                style: {
                    image: img,
                    width: 140,
                    height: 140
                },
                left: 'center',
                top: 'center',
                position: [100, 100]
            }]
        },
        tooltip: {
            show: false
        },
        toolbox: {
            show: false
        },
        series: seriesOption
    }
    myChart.setOption(option);
}